import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { NavigationExtras, Router } from '@angular/router';
import { ApiService } from 'src/app/services/api.service';
import { Subject } from 'rxjs';
import { ToastrService } from 'ngx-toastr';
import { ToastController } from '@ionic/angular';
import { Location } from '@angular/common';
import { NgOtpInputComponent } from 'ng-otp-input';
import { DataService } from 'src/app/services/data.service';

export class otpModel {
  source_key: any;
  source_value: any;
  source: any;
  isMobileLogin: boolean;
}
export class verifyotpModel {
  sourceKey: any;
  sourceValue: any;
  otp: number;
  type: any;
  isMobileLogin: boolean;
}
@Component({
  selector: 'app-otp',
  templateUrl: './otp.page.html',
  styleUrls: ['./otp.page.scss'],
})
export class OtpPage implements OnInit {
  @ViewChild('ngOtpInput', { static: false }) ngOtpInput: NgOtpInputComponent;
  otpValue: any = null;
  otpForm: FormGroup;
  otpValid: boolean = false;
  verifyOtpModel = new verifyotpModel();
  oTpModel = new otpModel();
  otpResponse: any;
  PhoneNumLogin: any;
  customerPhonenum: any;
  otpToken: number;
  showPassword: boolean = false;
  showOtp: boolean = false;
  userPasswordUpdate = new Subject<string>();
  otp = new FormControl();
  routerData: any;
  navSubscription: any;
  screenNames: any;
  countryCode: string = '+91';
  errMsg: string = '';
  isLoading: boolean = false;

  config = {
    allowNumbersOnly: true,
    length: 6,
    isPasswordInput: true,
    disableAutoFocus: false,
    placeholder: '',
    inputStyles: {
      'width': '40px',
      'height': '40px'
    }
  };
  formattedOtp: any;

  constructor(
    private cdk: ChangeDetectorRef,
    private router: Router,
    private fb: FormBuilder,
    private api: ApiService,
    private toastr: ToastrService,
    private toastCtrl: ToastController,
    private _location: Location,
    private dataService: DataService
  ) { }

  ngOnInit() {
    this.customerPhonenum = localStorage.getItem('customerPhonenum');
    this.otpForm = this.fb.group({
      phoneNo: ['', [Validators.required]],
      otp: ['', [Validators.required]],
      password: ['', [Validators.required]],
    });
    this.PhoneNumLogin = localStorage.getItem('customerPhonenum');

    this.navSubscription = this.api.getNavParam.subscribe(
      (data: any) => (this.screenNames = data));

  }

  onOtpChange() {

  }

  getOTP() {
    this.oTpModel.source = 'customer';
    this.oTpModel.source_key = 'mobile';
    this.oTpModel.source_value = this.customerPhonenum;
    this.oTpModel.isMobileLogin = true;

    this.api.getOtp(this.oTpModel).subscribe((res: any) => {
      if (res.status == 200) {
        this.openToast(res?.message);
      } else {
        this.openToast(res?.message);
      }
    }, (err: any) => {
      this.openToast(err);
    });;
  }

  resendOTP() {
    this.ngOtpInput.setValue('');
    this.getOTP();
  }

  validateOtp() {
    console.log(this.customerPhonenum);
    this.verifyOtpModel.sourceKey = 'mobile';
    this.verifyOtpModel.sourceValue = this.customerPhonenum;
    this.verifyOtpModel.otp = this.otp.value;
    this.verifyOtpModel.type = '';
    this.verifyOtpModel.isMobileLogin = true;

    this.isLoading = true;

    this.api.verifyOtp(this.verifyOtpModel).subscribe((otpResp: any) => {
      if (otpResp?.status == 200) {
        if (otpResp['data']?.firstTimeLogin == "Y") {
          this.router.navigateByUrl('/new-passwordchange');
        }
        else {
          // TODO:
          if (this.screenNames) {
            if (this.screenNames.queryParams.screenName == "mpinotpValidate" || this.screenNames.queryParams.screenName == "forgotmpin") {
              const navigationExtras: NavigationExtras = {
                queryParams: {
                  'screenName': 'forgotmpin'
                },
              };
              this.api.sendNavParam(navigationExtras);
              this.router.navigateByUrl('/setmpin');
            } else if (this.screenNames.queryParams.screenName == "forget-password") {
              const navigationExtras: NavigationExtras = {
                queryParams: {
                  'screenName': 'forget-password'
                },
              };
              this.api.sendNavParam(navigationExtras);
              this.router.navigateByUrl('/new-passwordchange');
            } else {
              sessionStorage.setItem('customer_id', otpResp['data']?.customerId);
              localStorage.setItem('firstName', otpResp['data']?.firstName);
              localStorage.setItem('lastName', otpResp['data']?.lastName);
              localStorage.setItem('customer_id', otpResp['data']?.customerId);
              localStorage.setItem('customer_details', JSON.stringify(otpResp['data']));
              localStorage.setItem('isShowed', "no");
              this.router.navigate(['dashboard'], { replaceUrl: true });
              this.otpForm.reset();
              this.dataService.isLoggedIn.next(true);
              this.openToast(otpResp?.message);
            }
          }
        }
      } else {
        this.router.navigateByUrl('/login');
        this.openToast(otpResp?.message);
      }
      this.isLoading = false;
    }, (err: any) => {
      this.errMsg = 'Please enter a valid OTP';
      setTimeout(() => this.errMsg = '', 3000);
      this.isLoading = false;
    });
  }

  goBack() {
    this.router.navigateByUrl('/login');
  }


  async openToast(message) {
    const toast = await this.toastCtrl.create({
      message: `${message}`,
      duration: 2500,
      position: 'bottom',
    });
    toast.present();
  }

  async openToast1(errorMessage) {
    const toast = await this.toastCtrl.create({
      message: `${errorMessage}`,
      duration: 2500,
      position: 'bottom',
    });
    toast.present();
  }

  back() {
    this._location.back();
  }

  obscureText() {
    this.config.isPasswordInput = !this.config.isPasswordInput;
    this.cdk.markForCheck();
  }
}
